.paper {
  padding: 30px;
}

.auth-form button {
  width: 80%;
  margin-left: 10%;
  padding: 7px 0;
}

.danger {
  color: red;
}

button:disabled {
  background: rgb(162, 224, 162);
}

/* .form-reg {
