input[type='text'],
input[type='password'],
input[type='date'],
select {
  max-width: 400px;
  padding: 15px;
  /* margin: 5px 0 22px 0; */
  /* display: inline-block; */
  border-image: conic-gradient(
      rgb(25, 200, 23),
      rgb(0, 128, 0),
      magenta,
      magenta,
      magenta
    )
    1;
  background: #f1f1f1;
}

textarea {
  min-width: 400px;
  max-width: 400px;
  min-height: 100px;
}

input[type='text']:focus,
input[type='password']:focus {
  background-color: #ddd;
  outline: none;
}

button {
  background-color: #4caf50;
  color: white;
  padding: 7px 20px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 100px;
  opacity: 0.9;
}

button:hover {
  opacity: 1;
}

a:hover {
  text-decoration: none;
  color: inherit;
}
